<template>
  <div :class="['home', $store.state.playShow ? 'stop-scroll' : '']">
    <div class="leftPopupBtn">
      <i class="iconfont icon-caidan" @click="showLeftPopup"></i>
    </div>

    <router-view />

    <!-- leftPopup -->
    <van-popup
      v-model="show"
      position="left"
      :style="{ height: '100%', width: '85%' }"
      get-container="body"
    >
      <leftPopup />
    </van-popup>

    <!-- tabbar -->
    <van-tabbar :border="false" v-model="active">
      <van-tabbar-item @click="toActive" name="discover">
        <span :class="active !== 'discover' ? '' : 'activeTextColor'"
          >发现</span
        >
        <template #icon>
          <div :class="['icon', active !== 'discover' ? '' : 'activeColor']">
            <img class="iconfont1" src="../assets/image/default.png" />
          </div>
        </template>
      </van-tabbar-item>
      <van-tabbar-item @click="toActive" name="myMusic">
        <span :class="active !== 'myMusic' ? '' : 'activeTextColor'">我的</span>
        <template #icon>
          <div :class="['icon', active !== 'myMusic' ? '' : 'activeColor']">
            <span class="iconfont icon-yinyue"></span>
          </div>
        </template>
      </van-tabbar-item>
      <van-tabbar-item @click="toActive" name="video">
        <span :class="active !== 'video' ? '' : 'activeTextColor'">视频</span>
        <template #icon>
          <div :class="['icon', active !== 'video' ? '' : 'activeColor']">
            <span class="iconfont icon-m"></span>
          </div>
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import leftPopup from "./leftPopup/leftPopup";
export default {
  components: {
    leftPopup,
  },
  data() {
    return {
      active: "",
      show: false,
      transitionName: "",
      showPlayList: false,
    };
  },
  created() {
    this.handelActive();
    this.$store.state.controlBarStyle = true;
  },
  methods: {
    toActive() {
      this.$router.push("/index/" + this.active);
      sessionStorage.setItem("active", this.active);
      // console.log("to", this.active);
    },
    /* 解决刷新页面tabbar激活与实际页面不符问题 */
    handelActive() {
      if (sessionStorage.getItem("active")) {
        this.active = sessionStorage.getItem("active");
        // console.log("else", this.active);
      } else {
        this.active = "discover";
      }
    },
    showLeftPopup() {
      this.show = true;
    },
  },
  beforeDestroy() {
    this.$store.state.controlBarStyle = false;
  },
};
</script>

<style scoped>
.home {
  position: relative;
}
.leftPopupBtn {
  z-index: 99;
  height: 10%;
  width: 20%;
  position: fixed;
  top: 0;
  background-color: #fff;
}
.leftPopupBtn .iconfont {
  font-size: 25px;
  position: relative;
  top: 25%;
  left: 25%;
}
.stop-scroll {
  overflow: hidden;
  position: fixed;
}
.van-tabbar {
  height: 65px;
  padding-bottom: 10px;
}
.icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: relative;
}
.activeColor {
  background-color: rgb(250, 90, 70);
  -webkit-filter: contrast(300%); /* Chrome, Safari, Opera */
  filter: contrast(300%);
  color: #fff;
}
.activeTextColor {
  color: rgb(253, 56, 53);
}
.icon .iconfont1 {
  width: 20px;
  height: 20px;
  position: absolute;
  margin: 25%;
}
.icon .iconfont {
  font-size: 20px;
  position: absolute;
  margin: 25%;
}
</style>
